import { ChangeDetectorRef, Component, Input, OnInit, SimpleChanges } from '@angular/core';
import { Router } from '@angular/router';
import { VhQueryCafe } from 'ionic-vhframeworks';

@Component({
  selector: 'app-kitchen',
  templateUrl: './kitchen.component.html',
  styleUrls: ['./kitchen.component.scss']
})
export class KitchenComponent implements OnInit {

  indexTab: number = 0;
  listTab: Array<any> = [{ index: 0, name: 'Take away' }, { index: 1, name: 'Table' }, { index: 2, name: 'Dishes' }]
  type: 'bills_takeaway' | 'bills_table' | 'products' = 'bills_takeaway'
  expands: boolean = false;
  public data: any = {
    bills_takeaway: [],
    bills_table: [],
    products: []
  };
  callbackKitchen: any;
  heightScroll: any;
  @Input() id_dispatcherzone = ''
  constructor(
    private cdRef: ChangeDetectorRef,
    private router: Router,
    private vhQueryCafe: VhQueryCafe
  ) { }

  ngOnInit(): void {
    // this.callbackKitchen = this.vhQueryCafe.observableDispatcher(this.id_dispatcherzone).subscribe(data => {
    //   console.log(data);

    //   this.data = {
    //     ...data,
    //     products: [
    //       [], []
    //     ]
    //   };
    //   data.products.forEach((dish, index) => {
    //     let item: any = dish.type == 5 ?
    //       { ...dish, show: this.expands, subComboName: this.getSubCombo(dish.bill_details[0].combos) } :
    //       { ...dish, show: this.expands }

    //     if (index % 2 == 0 || index == 0) this.data['products'][0].push(item)
    //     else this.data['products'][1].push(item)
    //   })

    // },(error:any) => {
    //   console.log('observableDispatcher error',error);
    //   })
  }

  ngOnChanges({ id_dispatcherzone }: SimpleChanges): void {
    console.log('id_dispatcherzone-------',this.id_dispatcherzone);
    if (this.callbackKitchen) {
      this.callbackKitchen.unsubscribe()
      this.vhQueryCafe.closeDispatcher()
    }
    setTimeout(() => {
      console.log(this.id_dispatcherzone);
      
      this.callbackKitchen = this.vhQueryCafe.observableDispatcher(this.id_dispatcherzone).subscribe(data => {
        console.log(data);

        this.data = {
          ...data,
          products: [
            [], []
          ]
        };
        data.products.forEach((dish, index) => {
          let item: any = dish.type == 5 ?
            { ...dish, show: this.expands, subComboName: this.getSubCombo(dish.bill_details[0].combos) } :
            { ...dish, show: this.expands }

          if (index % 2 == 0 || index == 0) this.data['products'][0].push(item)
          else this.data['products'][1].push(item)
        })

      },(error:any) => {
        console.log('observableDispatcher error',error);
        })
    }, 200);

  }


  areaChanged(index) {
    switch (index) {
      case 0: this.type = 'bills_takeaway'; break;
      case 1: this.type = 'bills_table'; break;
      case 2: this.type = 'products'; break;
    }
  }
  ngAfterViewChecked() {
    if (document.querySelector('#kitchen-management')) this.heightScroll = document.querySelector('#kitchen-management').clientHeight - 80 + 'px'
    this.cdRef.detectChanges()
  }
  ngOnDestroy() {
    if (this.callbackKitchen) {
      this.callbackKitchen.unsubscribe()
      this.vhQueryCafe.closeDispatcher()
    }

  }
  goBack() {
    this.router.navigate(['/cafe/coordination'], { state: { isSync: true } });
  }

  /**Render các thành phần combo */
  getSubCombo(arr: any[]) {
    let subName: string = ""
    arr.forEach(item => {
      if (subName.length)
        subName += `, ${item.quantity} ${item.name}`
      else subName += `${item.quantity} ${item.name}`
    })
    return subName
  }

  expandsAll() {
    this.expands = !this.expands
    this.data['products'][0].forEach(item => { item['show'] = this.expands })
    this.data['products'][1].forEach(item => { item['show'] = this.expands })
  }

}
