import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { VhQueryCafe } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-modal-kitchen',
  templateUrl: './modal-kitchen.component.html',
  styleUrls: ['./modal-kitchen.component.scss']
})
export class ModalKitchenComponent implements OnChanges {
  @Input('visible') visible: boolean = false;
  @Input('bill_details') bill_details: any;
  @Output("closeAndReceiveData") closeAndReceiveData = new EventEmitter<any>();
  title: string = '';
  constructor(
    private vhQueryCafe: VhQueryCafe,
    private modal: NzModalService,
    private lang: LanguageService
  ) { }
  public myTrackByFunction(index: number, complexItem: any): number {
    return complexItem._id;
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['bill_details'] && changes['bill_details']['currentValue']) {
      // console.log("change: ",changes['bill_details'])
      // nạp bill detail vào lại
      this.bill_details = changes['bill_details']['currentValue'].map(item => {
        let detail: any = { ...item, img: this.vhQueryCafe.getlocalProduct(item.id_product).img, name: this.getNameFood(item.id_product, item.id_subproduct) }

        if (detail.ptype == 5) detail['subComboName'] = this.getSubCombo(detail.combos)
        return detail
      })
      this.title = this.bill_details[0].id_table != 'go_home' ? this.bill_details[0].table_name : this.bill_details[0].bill_code
    }
  }

  exportDish(item, name) {
    this.modal.confirm({
      nzTitle: this.lang.translate("The dish is ready"),
      nzContent: `${name + ' '} ${item.table_name == "Take away" ? this.lang.translate("of order code") + " " + item.bill_code + " " + this.lang.translate("take away") : this.lang.translate("of table") + " " + item.table_name
        }`,
      nzOkText: "OK",
      nzCancelText: this.lang.translate("Cancel"),
      nzOnOk: () => {
        this.vhQueryCafe.doneDispatcher(item._id).then(() => {
          if (!this.bill_details.length) this.cancelTakeaway()
        })
      },
      nzOnCancel: () => { }
    });
  }
  getNameFood(id_prod: string, id_subProd: string) {
    let food = this.vhQueryCafe.getlocalProduct(id_prod)
    let subFood: any
    if (id_subProd) subFood = food.subs.find(item => item._id == id_subProd)
    return `${food ? food.name : ''} ${subFood ? '(' + subFood.name + ')' : ''}`
  }
  /**Render các thành phần combo */
  getSubCombo(arr: any[]) {
    let subName: string = ""
    arr.forEach(item => {
      if (subName.length)
        subName += `, ${item.quantity} ${item.name}`
      else subName += `${item.quantity} ${item.name}`
    })
    return subName
  }
  cancelTakeaway() {
    this.closeAndReceiveData.emit(false)
  }
}
