import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-detail-kitchen',
  templateUrl: './detail-kitchen.component.html',
  styleUrls: ['./detail-kitchen.component.scss']
})
export class DetailKitchenComponent implements OnInit {

  bill_details: Array<any> = this.router.getCurrentNavigation().extras.state.bill_details
  bill_details_show: Array<any> = []
  title: string = '';
  constructor(
    private router: Router,
    private lang: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    private vhComponent: VhComponent,
  ) {

  }

  ngOnInit(): void {
    this.title = this.bill_details[0].id_table != 'go_home' ? this.bill_details[0].table_name : this.bill_details[0].bill_code
    this.bill_details_show = this.bill_details.map(item => { return { ...item, img: this.vhQueryCafe.getlocalProduct(item.id_product).img, name_product: this.getNameFood(item.id_product, item.id_subproduct) } })
  }

  public myTrackByFunction(index: number, complexItem: any): number {
    return complexItem._id;
  }
  getNameFood(id_prod: string, id_subProd: string) {
    let food = this.vhQueryCafe.getlocalProduct(id_prod)
    let subFood: any
    if (id_subProd) subFood = food.subs.find(item => item._id == id_subProd)
    return `${food ? food.name : ''} ${subFood ? '(' + subFood.name + ')' : ''}`
  }
  exportDish(item, name: string) {
    this.vhComponent.alertConfirm(this.lang.translate("Confirm"), `${item.table_name == "Take away" ? this.lang.translate(item.table_name) : item.table_name}`, `${name} ${item.note ? ("- " + item.note) : ''}`, "Ok", this.lang.translate("Cancel")).then(() => {
      this.vhQueryCafe.doneDispatcher(item._id).then(() => {
        let index = this.bill_details_show.findIndex(element => element._id == item._id)
        this.bill_details_show.splice(index, 1)
        if (!this.bill_details_show.length) this.goBack()
      })
    }, () => { })
  }
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/coordination'])
  }
}
